<template>
  <v-container fluid>
    <v-layout>
      <navbar color="grey darken-3" textcolor="white--text"></navbar>
      <v-flex row xs3 md3 lg3 xl3>
        <menuoptions></menuoptions>
      </v-flex>
      <v-flex row xs11 md11 lg11 xl11>



        <v-container fluid class="mt-5">
          <v-tabs centered color="grey darken-2" dark icons-and-text>
            <v-tabs-slider color="blue"></v-tabs-slider>

            <v-tab href="#tab-1">
              Mi Plantilla
              <v-icon>ar fa-list-alt</v-icon>
            </v-tab>

            <v-tab href="#tab-2" v-if="g_loginUser.AllUserSupervisers.length > 0">
              Plantila Supervisores
              <v-icon>far fa-list-alt</v-icon>
            </v-tab>

            <v-tab href="#tab-3">
              Escala de distribucin
              <v-icon>far fa-list-alt</v-icon>
            </v-tab>


            <v-tab-item :value="'tab-1'">
              <v-container fluid>
                <v-toolbar dark color="blue darken-3 white--text " text-xs-center>
                  <h5><i class="fas fa-edit mr-3"></i>Proceso de Evaluaciones</h5>
                  <v-spacer></v-spacer>

                </v-toolbar>
                <listaempleados from="EVAL" :headers="headerSumarySupervisor"
                  v-if="g_loginUser.SuperviserSummary.length > 0" :list='g_loginUser.SuperviserSummary' excelname=''
                  hassearch=false haspagination=true>
                </listaempleados>
              </v-container>

              <v-container fluid>
                <v-toolbar dark color="blue darken-3 white--text">
                  <h4><i class="far fa-list-alt mr-3"></i>Mi Plantilla</h4>
                  <v-spacer></v-spacer>
                </v-toolbar>
                <listaempleados :headers="headers" v-if="g_loginUser.Subordinates.length > 0"
                  :list='g_loginUser.Subordinates' excelname="empleadosEvaluadores.xls" haspagination=false>
                </listaempleados>
              </v-container>

            </v-tab-item>

            <v-tab-item :value="'tab-2'" v-if="g_loginUser.AllUserSupervisers.length > 0">
              <v-container fluid>
                <v-toolbar dark color="blue darken-3 white--text">
                  <h4><i class="far fa-list-alt mr-3"></i>Plantila de supervisores</h4>
                  <v-spacer></v-spacer>
                </v-toolbar>
                <listaempleados from="EVAL" :headers="headers" :list='g_loginUser.AllUserSupervisers'
                  excelname="PantillaSupervisores.xls" haspagination=true>
                </listaempleados>
              </v-container>
            </v-tab-item>

            <v-tab-item :value="'tab-3'">
              <v-container fluid>
                <v-toolbar dark color="blue darken-3 white--text">
                  <h4><i class="far fa-list-alt mr-3"></i>Escala de distribucin</h4>
                  <v-spacer></v-spacer>
                </v-toolbar>
                <listaempleados from="EVAL" :headers="headersDistribucion"
                  v-if="g_loginUser.DistributionSuperviser.length > 0" :list='g_loginUser.DistributionSuperviser'
                  excelname="escaladistribuciontodosempleados.xls" haspagination=false>
                </listaempleados>
              </v-container>
            </v-tab-item>
          </v-tabs>

        </v-container>





      </v-flex>
    </v-layout>


  </v-container>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";

import listaempleados from "../components/listaempleados.vue";
import navbar from "../components/navbar.vue";
import menuoptions from "../components/menuoptions.vue";
import escaladistribucion from "../components/escaladistribucion.vue";

export default {
  name: "mempleadosaevaluar",
  components: {
    menuoptions,
    navbar,
    listaempleados,
    escaladistribucion
  },
  data() {
    return {
      color: "green lighten-1",
      tabs: null,
      text:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",
      headerSumarySupervisor: [
        {
          text: "Nmero de Empleados",
          type: "text",
          align: "center",
          resize: false,
          value: "TotalEvaluate",
          tooltip: "Total Epleados a Evaluar"
        },
        {
          text: "No Iniciados",
          type: "text",
          align: "center",
          resize: false,
          value: "NoIniciados",
          tooltip: "Numero de Empleados que no se ha iniciado su evaluacin"
        },
        {
          text: "Iniciado",
          type: "text",
          align: "center",
          resize: false,
          value: "Initiated",
          tooltip:
            "Numero de Empleados que ya se inici su evaluacin pero que aun faltan puntos por califica"
        },
        {
          text: "Completado",
          type: "text",
          align: "center",
          resize: false,
          value: "Completed",
          tooltip:
            "Numero de Empleados que ya se terminaron de evaluar y est pendiente su retroalimentacin"
        },
        {
          text: "Terminado",
          align: "center",
          type: "text",
          resize: false,
          value: "Evaluated",
          tooltip:
            " Numero de Empleados con el proceso finalizado (evaluacin, retroalimentacin y firmas)"
        }
      ],
      headers: [
        {
          text: ".",
          type: "image",
          align: "center",
          value: "Image",
          resize: true
        },
        {
          text: "Num Emp",
          type: "text",
          align: "center",
          value: "Number",
          resize: false
        },
        {
          text: "Nombre",
          type: "text",
          align: "center",
          value: "PrettyName",
          resize: true
        },
        {
          text: "Clasificacin",
          type: "text",
          align: "center",
          value: "Clasificacion",
          resize: true
        },
        {
          text: "Puesto",
          type: "text",
          align: "center",
          value: "Position",
          resize: true
        },
        {
          text: "Status",
          type: "text",
          align: "center",
          value: "Status",
          resize: true
        },
        {
          text: "Evaluacin",
          type: "text",
          align: "center",
          value: "Score",
          resize: true
        },
        {
          text: "",
          type: "button",
          align: "left",
          btntitle: "Evaluar",
          action: "action_evaluarEmpleado",
          has_condition: true,
          condition_property: "Status",
          getconditiontext: true,
          conditionvalues: [
            {
              condition: "NO INICIADO",
              text: "Evaluar",
              color: "indigo darken-4"
            },
            {
              condition: "EVALUADO",
              text: "Editar",
              color: "blue darken-3"
            },
            {
              condition: "INICIADO",
              text: "Editar",
              color: "blue darken-3"
            },
            {
              condition: "COMPLETADO",
              text: "Editar",
              color: "blue darken-3"
            }
          ],
          resize: true,
          value: ""
        }
      ],
      headersDistribucion: [
        {
          text: "",
          type: "text",
          align: "center",
          value: "ValueDefinition",
          resize: true
        },
        {
          text: "Puntuacin",
          type: "text",
          align: "center",
          value: "Puntuation",
          resize: true
        },

        {
          text: "% de Distribucin",
          type: "text",
          align: "center",
          value: "Percentage",
          resize: true
        },
        {
          text: "Evaluacin",
          type: "text",
          align: "center",
          value: "Score",
          resize: true
        },
        {
          text: "Capacidad Maxima",
          type: "text",
          align: "center",
          value: "Quantity",
          resize: true
        },
        {
          text: "Personas Evaluadas",
          type: "text",
          align: "center",
          value: "Evaluated",
          resize: true
        },
        {
          text: "Nombre Personas",
          type: "combo",
          align: "center",
          value: "distributionByEmployee",
          iconbtn: "fa fa-users",
          titlecombo: "PrettyName",
          valuecombo: "Number",
          action: "action_evaluarEmpleado",
          resize: true
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["g_loginUser"]),
    ...mapState(["token"])
  },
  methods: {
    escalaDis(Score) {
      var esto = this;

      var dialog = {
        Value: true,
        Title: " Escala distribucion " + Score.Score + " ",
        Subtitle:
          Score.ValueDefinition + "  porcentaje " + Score.Percentage + "%",
        contieneImagen: false,
        Image: "",
        Paragraph: "",
        component: {
          type: "list",
          list: esto.g_loginUser.Subordinates.filter(function(el) {
            return el.Score == Score.Score;
          }),
          dindex: "Number",
          title: "PrettyName",
          subtitle: "Position",
          avatar: "Image"
        }
      };

      this.$store.dispatch("sw_dialog", dialog);
    }
  },
  created() {
    var esto = this;
    esto.$store.dispatch("s_Loading", {
      value: 0,
      show: true
    }),
      esto.$store.dispatch("GetSubordinateByUser");
    esto.$store.dispatch("set_returnto", "miplantilla");
  }
};
</script>

<style scope>
@import "../assets/css/global.css";

@import "../assets/css/media_query.css";

.mr-10 {
  margin-right: 80px;
}

a:hover {
  color: #d1d2d3 !important;
}
</style>



